import React from 'react'
import PropTypes from 'prop-types'
import { StaticQuery, graphql } from 'gatsby'
import ThemeProvider from '@material-ui/styles/ThemeProvider'

import Header from '../header/header'
import Footer from '../footer/footer'
import GaSnackbar from '../ga-snackbar/ga-snackbar'

import theme from '../../services/theme'
import './layout.css'

const Layout = ({ children }) => {
  return (
    <StaticQuery
      query={graphql`
        query SiteTitleQuery {
          site {
            siteMetadata {
              title
            }
          }
        }
      `}
      render={data => {
        const { title } = data.site.siteMetadata
        return (
          <div style={{ minHeight: '100vh' }}>
            <ThemeProvider theme={theme}>
              <Header siteTitle={title} />
              <main
                style={{
                  margin: `0 auto`,
                  maxWidth: theme.breakpoints.width('xl'),
                }}
              >
                {children}
              </main>
              <Footer />
              <GaSnackbar
                gaProperty={'UA-150649342-1'}
                message="Wenn Sie Google Analytics deaktivieren möchten, klicken Sie bitte auf `Opt-Out`"
                labels={{
                  optOut: 'Opt-Out',
                  consent: 'Okay, track me',
                }}
              />
            </ThemeProvider>
          </div>
        )
      }}
    />
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
