import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'

import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/styles/withStyles'

import RandomFooterText from '../../services/random-footer-text'

const styles = theme => ({
  root: {
    color: '#fff',
    background: '#313131',
    padding: theme.spacing(4),
  },
  inner: {
    padding: theme.spacing(2),
    flexGrow: '1',
  },
})

const EasterEgg = (props) => {
  return <span {...props}>{props.message}</span>
}

EasterEgg.propType = {
  message: PropTypes.string.isRequired,
}

const Footer = ({ classes }) => {
  const [message, updateMessage] = React.useState(RandomFooterText())
  return (
    <footer className={classes.root}>
      <Typography component={'p'} align={'center'} className={classes.inner}>
        {'-· '}
        &copy; {new Date().getFullYear()} compose.us GmbH
        {' · '}
        Kontaktiert uns via{' '}
        <a href="mailto:hello@compose.us">hello@compose.us</a>
        {' · '}
        <Link to={'/impressum/'}>Impressum</Link>
        {' ·-'}
        <br />
        <EasterEgg message={message} onMouseEnter={() => updateMessage(RandomFooterText())} />
      </Typography>
    </footer>
  )
}

Footer.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(Footer)
